@keyframes oval-loader-animation{0%{transform:rotate(0deg)}to{transform:rotate(1turn)}}.loaderWrapper{align-items:center;background:hsla(0,0%,100%,.75);display:flex;height:100%;justify-content:center;position:absolute;width:100%;z-index:1000}.loader{display:inline-block}.loader,.loader:after{height:36px;width:36px}.loader:after{animation:oval-loader-animation 1.2s linear infinite;border:4.5px solid #228be6;border-left-color:transparent;border-radius:10000px;content:"";display:block}