.headerRow{display:grid;font-size:.875rem;font-weight:600;grid-template-columns:repeat(1,minmax(0,1fr));margin-left:2.25rem;margin-right:.5rem;position:relative}.headerRow[data-sm=true]{font-size:.75rem}.headerRow[data-isweekview=true]{grid-template-columns:repeat(7,minmax(0,1fr))}.headerCell{text-align:center}.headerCell[data-border=true]{border-bottom:1px solid var(--ec-gray-4)}.headerLabel{background:transparent;padding:.25rem 0;user-select:none}.headerLabel[data-today=true]{background:#329aef;color:#fff}