.scrollWrapper{height:100%;margin:.125rem;overflow:auto;padding:.75rem 1rem 2rem}.yearView{column-gap:2rem;display:grid;flex-wrap:wrap;grid-template-columns:repeat(auto-fit,minmax(200px,1fr));margin:0 auto;max-width:1280px;row-gap:1rem}.month{display:flex;flex-direction:column;gap:.25rem}.monthDates{display:grid;grid-template-columns:repeat(7,1fr);row-gap:.25rem}.monthLabel{font-size:.875rem;margin-top:.25rem;place-content:center;width:100%}.monthHeaderDay,.monthLabel{display:grid;font-weight:500;user-select:none}.monthHeaderDay{color:#868e96;font-size:.75rem;height:1.375rem;place-content:center;width:1.375rem}