.grid,.monthView{display:flex;flex-direction:column;height:100%}.row{border-bottom:1px solid var(--ec-gray-3);display:grid;flex-grow:1;grid-template-columns:repeat(7,minmax(0,1fr));position:relative}.row:last-child{border-bottom:none}.datePopoverTitle{font-size:1rem;font-weight:600;margin-bottom:.5rem;text-align:center}.dropdown{border:none;cursor:auto;padding:0;pointer-events:auto}