.date{border-radius:9999px;color:var(--ec-gray-9);cursor:pointer;display:grid;font-size:.7rem;height:1.375rem;line-height:1.375rem;place-content:center;user-select:none;width:1.375rem}.date:hover{background-color:var(--ec-gray-1)}.date[data-dimmed=true]{color:var(--ec-gray-5)}.date[data-active=true]{background-color:#20c997;color:#fff}.date[data-active=true]:hover{background-color:#12b886}.date[data-today=true]{background-color:#228be6;color:#fff}